body {
  letter-spacing: 1px;
  color: #6c788b;
  font-family: Open Sans, sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 1.5;
}

[class*="__container"] {
  width: 100%;
  max-width: 1254px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 640px) {
  [class*="__container"] {
    padding: 0 40px;
  }
}

@media (width >= 1024px) {
  [class*="__container"] {
    padding: 0 54px;
  }
}

.wrapper {
  flex-direction: column;
  min-height: 100svh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.wrapper > .page {
  flex-grow: 1;
}

.logo {
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 25px;
  display: flex;
}

.logo__img {
  object-fit: contain;
  object-position: center;
  width: 100%;
  height: 100%;
}

.nav__link {
  color: #fff;
  letter-spacing: .3px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-family: Poppins, serif;
  font-weight: 600;
  position: relative;
}

.nav__link:before {
  content: "";
  background-color: #fff;
  border-radius: 4px;
  width: 0;
  height: 1px;
  transition-duration: .3s;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.nav__link:hover:before {
  width: 100%;
}

.socials__wrapper {
  align-items: center;
  gap: 32px;
  display: flex;
}

@media (width >= 640px) {
  .socials__wrapper {
    gap: 36px;
  }
}

.socials__icon {
  color: #fff;
  cursor: pointer;
  font-size: 32px;
  transition-duration: .3s;
}

.socials__icon:hover {
  color: #2060f6;
}

.button {
  text-align: center;
  color: #fff;
  background-color: #2060f6;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 280px;
  padding: 14px;
  font-family: Poppins, serif;
  font-size: 15px;
  font-weight: 600;
  display: flex;
  overflow: hidden;
}

.button:hover {
  filter: brightness(1.1);
}

.hire-us {
  color: #fff;
  letter-spacing: .3px;
  text-transform: uppercase;
  letter-spacing: 3px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-family: Poppins, serif;
  font-weight: 700;
  display: flex;
  position: relative;
}

.hire-us:before {
  content: "";
  background-color: #2060f6;
  border-radius: 5px;
  width: 100%;
  height: 2px;
  transition-duration: .3s;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.hire-us:hover:before {
  width: 0;
}

.section__header:not(:last-child) {
  margin-bottom: 24px;
}

.section__header--big:not(:last-child) {
  margin-bottom: 48px;
}

@media (width >= 640px) {
  .section__header:not(:last-child) {
    margin-bottom: 16px;
  }

  .section__header--big:not(:last-child) {
    margin-bottom: 56px;
  }
}

@media (width >= 1024px) {
  .section__header--big:not(:last-child) {
    margin-bottom: 85px;
  }
}

.section__title {
  color: #253757;
  letter-spacing: .3px;
  text-align: center;
  font-family: Poppins, serif;
  font-size: 32px;
  font-weight: 600;
}

.section__title--big {
  font-size: 44px;
}

.section__title--start {
  text-align: start;
  width: 100%;
  max-width: 264px;
  line-height: 1.2;
}

.section__title:not(:last-child) {
  margin-bottom: 24px;
}

@media (width >= 640px) {
  .section__title--big {
    font-size: 52px;
  }
}

.section__subtitle {
  color: #c0cdd7;
  letter-spacing: .3px;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-family: Poppins, serif;
  font-weight: 600;
}

.section__subtitle:not(:last-child) {
  margin-bottom: 16px;
}

.section__block {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.section__text {
  width: 100%;
  max-width: 264px;
  font-size: 14px;
  font-weight: 400;
}

.section__main {
  justify-content: center;
  align-items: center;
  display: flex;
}

.section__main:not(:last-child) {
  margin-bottom: 56px;
}

.section__footer {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  z-index: 10;
  background-color: #2c2c2c;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.header__wrapper {
  padding: 20px 0;
  position: relative;
}

@media (width >= 1024px) {
  .header__wrapper {
    padding: 48px 0 30px;
  }
}

.header__content {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__block {
  align-items: center;
  gap: 32px;
  display: flex;
}

.nav-header {
  opacity: 0;
  background-color: #2c2c2c;
  width: 100vw;
  height: calc(100svh - 80px);
  transition-duration: .3s;
  position: fixed;
  top: 80px;
  left: 0;
  overflow-y: auto;
  transform: translateX(-100%);
}

@media (width >= 1024px) {
  .nav-header {
    left: unset;
    top: unset;
    overflow-y: unset;
    opacity: 1;
    width: max-content;
    height: auto;
    position: relative;
    transform: translateX(0);
  }
}

.nav-header__wrapper {
  padding: 30px 0;
}

@media (width >= 1024px) {
  .nav-header__wrapper {
    padding: 0;
  }
}

.nav-header__list {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

@media (width >= 1024px) {
  .nav-header__list {
    flex-direction: row;
    gap: 48px;
  }
}

.nav-header--active {
  opacity: 1;
  transform: translateX(0);
}

.burger {
  width: 24px;
  height: 24px;
  padding: 0 3px;
}

@media (width >= 1024px) {
  .burger {
    display: none;
  }
}

.burger__wrapper {
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  height: 100%;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.burger__wrapper:hover {
  transform: scale(1.1);
}

.burger__item {
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  height: 2px;
  transition-duration: .3s;
}

.burger--active .burger__item {
  position: absolute;
  top: 12px;
}

.burger--active .burger__item:first-child {
  left: 0;
  transform: rotate(45deg);
}

.burger--active .burger__item:nth-child(2) {
  right: 0;
  transform: rotate(-45deg);
}

.hero__wrapper {
  color: #fff;
  background-color: #2c2c2c;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  flex-direction: column;
  gap: 72px;
  padding-top: 110px;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (width >= 640px) {
  .hero__wrapper {
    gap: 140px;
    padding-top: 120px;
  }
}

@media (width >= 1024px) {
  .hero__wrapper {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    align-items: center;
    gap: 50px;
    padding-top: 140px;
    display: grid;
  }

  .hero__container {
    justify-content: end;
    display: flex;
  }
}

.hero__block {
  flex-direction: column;
  gap: 72px;
  display: flex;
}

@media (width >= 640px) {
  .hero__block {
    gap: 56px;
  }
}

@media (width >= 1024px) {
  .hero__block {
    gap: 83px;
  }
}

.hero__titles {
  flex-direction: column;
  gap: 32px;
  display: flex;
}

@media (width >= 640px) {
  .hero__titles {
    gap: 24px;
  }
}

@media (width >= 1024px) {
  .hero__titles {
    gap: 32px;
  }
}

.hero__title {
  color: #fff;
  letter-spacing: .3px;
  letter-spacing: -2px;
  font-family: Poppins, serif;
  font-size: 52px;
  font-weight: 600;
  line-height: 1.2;
}

@media (width >= 640px) {
  .hero__title {
    font-size: 64px;
  }
}

@media (width >= 1024px) {
  .hero__title {
    width: 100%;
    max-width: 490px;
  }
}

.hero__text {
  letter-spacing: 1.5px;
  width: 100%;
  max-width: 362px;
  font-size: 18px;
  font-weight: 400;
}

.hero__slider {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  overflow: hidden;
}

@media (width >= 1024px) {
  .hero__slider {
    border-top-right-radius: 0;
  }
}

.swiper {
  height: 100vh;
  max-height: 390px;
}

@media (width >= 1024px) {
  .swiper {
    max-height: 680px;
  }
}

.swiper-slide {
  height: 100%;
}

.slide-card {
  justify-content: start;
  align-items: end;
  min-height: 100%;
  display: flex;
  position: relative;
}

.slide-card:before {
  content: "";
  z-index: 1;
  background-color: #2c2c2c4d;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.slide-card__img {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
}

.slide-card__img img {
  object-position: center;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.slide-card__content {
  z-index: 2;
  flex-direction: column;
  gap: 16px;
  padding: 20px 24px;
  display: flex;
  position: relative;
}

@media (width >= 640px) {
  .slide-card__content {
    padding: 40px 32px;
  }
}

@media (width >= 1024px) {
  .slide-card__content {
    flex-direction: row;
    gap: 18px;
    padding: 20px 78px 66px;
  }
}

.slide-card__buttons {
  align-items: center;
  gap: 16px;
  display: flex;
}

.slide-card__button {
  cursor: pointer;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.slide-card__button:before {
  content: "";
  content: "";
  z-index: 2;
  color: #2c2c2c;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  font-family: icomoon;
  font-size: 16px;
  display: flex;
  position: relative;
}

.slide-card__button:hover {
  transform: scale(1.1);
}

.slide-card__button--prev:before {
  transform: rotate(180deg);
}

.slide-card__block {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.slide-card__title {
  color: #fff;
  letter-spacing: .3px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-family: Poppins, serif;
  font-weight: 700;
}

.slide-card__text {
  width: 100%;
  max-width: 280px;
  font-size: 14px;
  font-weight: 400;
}

